/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {

	.eltdf-content .eltdf-content-inner>.eltdf-container>.eltdf-container-inner{
		padding-bottom: 30px;
	}
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);

							@include ipad-landscape{
								width: 130%;
							}
							@include ipad-portrait{
								width: 100%;
							}
							@include phone-landscape{
								left: 0;
							}
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 10px 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;

				@include phone-landscape{
					margin: 0;
				}
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;

					@include ipad-landscape{
						width: 130%;
					}
					@include ipad-portrait{
						width: 100%;
					}

					@include phone-landscape{
						left: 0;
					}
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 27px 0 7px;
	    }
	
	    .price {
		    @include eltdfWooCommercePriceStyle();
			margin: 13px 0 0;
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 3px;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 26px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
		.eltdf-woo-social-share-holder{
			.eltdf-share-link{
				@include eltdfBody();
			}

			ul{
				margin: 8px 0;

				a{
					&:hover{
						color: $first-main-color;
					}
				}
			}

			.eltdf-social-title{
				margin-right: 5px;
				text-transform: uppercase;
			}
		}

	    .product_meta {
		    margin: 54px 0 5px;
		
		    > span {
			    display: block;
				margin: 13px 0 0;
				@include eltdfH6();
				font-size: 17px;
				color: $third-main-color;
				text-transform: uppercase;

				>span,
				a{
					@include eltdfBody();
					text-transform: none;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 48px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 2px 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 100px 0 0;

		@include phone-landscape{
			margin: 50px 0 0;
		}

		.eltdf-product-tabs-line {
			position: absolute!important;
			left: 0;
			bottom: -1px;
			height: 0;
			border-top: 1px solid $third-main-color;
			transition: width .4s, left .6s cubic-bezier(0.25, 0.1, 0.11, .99);

			@include phone-landscape{
				display: none;
			}
		}

		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				border-bottom: 1px solid #e4e4e4;
				margin-right: -3px;

				&.active{
					a{
						@include ipad-portrait{
							color: $first-main-color!important;
						}
					}
				}

				@include ipad-portrait{
					display: block;
					border-bottom: none;

					&.eltdf-product-tabs-line{
						display: none;
					}
				}

				&:hover,
				&.active{
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 34px;
					@include eltdfH6();
					text-transform: uppercase;

					&:hover{
						color: inherit;
					}
				}
			}
		}

		.woocommerce-Tabs-panel--description{
			text-align: center;
		}

		.entry-content {
			@include eltdfRelativeHolderLayout();
			width: 61%;
			margin-left: 51%;
			transform: translateX(-50%);

			@include ipad-landscape{
				width: 82%;
			}

			@include phone-landscape{
				width: 100%;
			}

			> h2 {
				display: none;
			}
			
			> p {
				margin: 22px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				margin: 15px 0 0;
				@include eltdfH6();
				font-size: 15px;
				color: $third-main-color;
				width: 21%;
				text-transform: uppercase;
			}
			
			td {
				@include eltdfBody();
				text-transform: none;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;

						.woocommerce-review__author{
							@include eltdfH6();
							font-size: 15px;
							color: $third-main-color;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 10px 0;
					}
					
					&.selected {
					
						a {
							@include eltdfWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						font-size: 0;
						line-height: 1;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 88px 0 4px;

		@include phone-landscape{
			margin: 38px 0 4px;
		}

		ul.products>.product{
			margin: 0!important;

			@include ipad-landscape{
				margin: 0 0 30px!important;
			}
			@include ipad-portrait{

			}

			@include phone-landscape{
				margin: 0 0 30px!important;
			}
		}

		> h2 {
			display: block;
			margin: 36px 0 20px;
			text-align: left;
			@include eltdfH3();
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */