/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */

.woocommerce-pagination {
	@include eltdfStandardPaginationStyle('shop');
	margin: 23px 0 0;

	ul{
		li{
			a{
				padding: 0 13px;

				&.next,
				&.prev{
					padding: 0 5px;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */