/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .eltdf-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
        }

        .eltdf-header-cart .eltdf-sc-opener-icon svg{
            stroke-width: 1.3px;
            
            rect,
            path{
                stroke: $first-main-color;
            }
        }

        .eltdf-sc-opener-count{
            color: $first-main-color;
        }
    }

    .eltdf-shopping-cart-inner {
        @include eltdfRelativeHolderLayout();
        height: 100%;

        .eltdf-sc-opener-count{
            margin-left: 3px;
            margin-bottom: 9px;
            font-size: 9px;
            font-family: $additional-font;
            transition: .2s;
        }
    }

    .eltdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .eltdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;

            svg{
                rect,
                path{
                    fill:transparent;
                    stroke: currentColor;
                    transition: .2s;
                }
            }
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-sc-opener-icon {
                font-size: 18px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: transparent;
                    stroke: currentColor;
                }
            }
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $first-main-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $first-main-color;
    }
}

.eltdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
    
    .eltdf-sc-dropdown-inner {
        @include eltdfRelativeHolderLayout();
        width: 333px;
        padding: 31px 31px;
        background-color: #f8f8f8;
        border: 1px solid #f8f8f8;
        box-sizing: border-box;

        .eltdf-empty-cart{
            color: $third-main-color;
        }
    }
    
    .eltdf-sc-dropdown-items {
        @include eltdfRelativeHolderLayout();
    }
    
    .eltdf-sc-dropdown-item {
        position: relative;
        display: block;
        align-items: flex-start;
        margin-top: 17px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .eltdf-sc-dropdown-item-image {
        width: 80px;
        margin-right: 18px;
        display: inline-block;
        vertical-align: middle;
        
        a, img {
            display: block;
        }
    }
    
    .eltdf-sc-dropdown-item-content {
        @include eltdfRelativeHolderLayout();
        padding-right: 20px;
        display: inline-block;
        width: calc(100% - 121px);
        vertical-align: middle;
        padding-right: 20px;

        .woocommerce-Price-amount{
            font-family: $subtitle-text-font;
            font-size: 16px;
        }
    }
    
    .eltdf-sc-dropdown-item-title {
        margin: 0;
        font-size: 16px;
        color: $third-main-color;
    }
    
    .eltdf-sc-dropdown-item-price {
        margin: 3px 0 0;
        color: $third-main-color;
    }
    
    .eltdf-sc-dropdown-item-quantity {
        margin: 3px 0;
        line-height: 1em;
        font-size: 15px;
        color: $default-text-color;
    }
    
    .eltdf-sc-dropdown-item-remove {
        position: absolute;
        top: -2px;
        right: 0;
        font-size: 20px;
        line-height: 1;

        svg{
            line{
                transition: .2s;
            }
        }

        svg{
            &:hover{
                line{
                    stroke: $first-main-color;
                }
            }
        }
    }
    
    .eltdf-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;

        .eltdf-sc-dropdown-total-amount{
            font-family: $subtitle-text-font;
            color: $third-main-color;
        }


        
        > * {
            margin: 0;
            font-size: 16px;
            color: $third-main-color;
        }
    }
    
    .eltdf-sc-dropdown-button-holder {
        @include eltdfRelativeHolderLayout();
        margin-top: 22px;

        a{
            font-family: $additional-font;
        }
    }
    
    .eltdf-sc-dropdown-button {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize(huge);
        @include eltdfButtonOutlineColor();
        padding: 10px 26px;
        color: $third-main-color;
        font-size: 13px;
        text-transform: none;

        &:hover {
            @include eltdfButtonOutlineHoverColor();
            color: $third-main-color;
            font-size: 13px;
            text-transform: none;
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */