/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 24px;
    width: auto;
    height: 25px;
    margin: 0;
    padding: 0 18px;
    color: #fff;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    z-index: 11;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin eltdfWooCommerceSaleStyle() {
    right: 25px;
    background-color: rgba($first-main-color, 1);
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    right: 25px;
    background-color: rgba($first-main-color, 1);
}

@mixin eltdfWooCommerceNewProductStyle() {
    left: 25px;
    background-color: rgba($first-main-color, 1);
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 5px 0 0;
    line-height: 1;
    @include eltdfH5();
    letter-spacing: .03em;
    color: $third-main-color;

    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($third-main-color, .8);
        @include eltdfH5();
        letter-spacing: .03em;
    }
    
    ins {
        text-decoration: none;
        @include eltdfH5();
        letter-spacing: .03em;
        color: $third-main-color;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 11px;
    line-height: 1;
    letter-spacing: 5px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin  eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    width: 78px;

    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\e031";
    $single-rating-active-icon: "\e033";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: $single-rating-active-icon;
            }
            
            ~ a {
    
                &:before {
                    content: $single-rating-default-icon;
                }
                &:after{
                    display: none;
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: $single-rating-active-icon;
            }
        }
        &:after{
            content: '\e031';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            color: #c9ab81;
            font-family: ElegantIcons;
            font-size: 11px;
        }
    } @else {
        
        &:before {
            content: $single-rating-default-icon;
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */