/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.eltdf-content .woocommerce {
    @include eltdfRelativeHolderLayout();
}

.eltdf-pl-main-holder {
    margin: 0 0 22px;
    
    .eltdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .eltdf-pl-inner .eltdf-pl-text {
                -webkit-clip-path: inset(0 0 0 0);
                clip-path: inset(0 0 0 0);
                transition: .4s cubic-bezier(0.48, 0.57, 0.33, 0.89);
            }

            .button,
            .added_to_cart {
                opacity: 1;
                transform: translateY(0);
                transition: .3s .1s;
            }
        }
        
        .eltdf-product-list-title {
            margin: 4px 0 4px;
            text-align: center;
        }
        
        .eltdf-pl-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 0 0 3px;
            text-align: center;
        }
        
        .price {
            @include eltdfWooCommercePriceStyle();
            text-align: center;
            font-size: 20px;
            line-height: 1.2em;
            letter-spacing: .03em;
            margin: 10px 0 0;

            del,ins{
                font-size: 20px;
                line-height: 1.2em;
                letter-spacing: .03em;
            }
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
            opacity: 0;
            transform: translateY(10px);
            transition: .3s;
    
            .eltdf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .eltdf-pl-inner {
            @include eltdfRelativeHolderLayout();
            overflow: hidden;
            
            .eltdf-pl-image {
                @include eltdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .eltdf-pl-text {
                @include eltdfAbsoluteHolderLayout();
                padding: 10px;
                z-index: 10;
                background-color: rgba(#fff, 1);
                width: calc(100% - 49px);
                height: calc(100% - 49px);
                margin-left: 50%;
                margin-top: 50%;
                transform: translate(-50%,-50%);
                box-sizing: border-box;
                -webkit-clip-path: inset(0 0 100% 0);
				clip-path: inset(0 0 100% 0);
				transition: .2s cubic-bezier(0.48, 0.57, 0.33, 0.89);
            }
            
            .eltdf-pl-text-outer {
                @include eltdfTableLayout();
                text-align: center;
            }
            
            .eltdf-pl-text-inner {
                @include eltdfTableCellLayout();
                
                .eltdf-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include eltdfAbsoluteHolderLayout();
            }
        }
        
        .eltdf-pl-text-wrapper {
            @include eltdfRelativeHolderLayout();
            margin: 21px 0 22px;
        }

        > a {
            @include eltdfAbsoluteHolderLayout();
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.eltdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .eltdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */