/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();
	
	&.eltdf-info-on-image {
		
		.eltdf-pli {
			
			.eltdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .eltdf-pli {
        padding: 0;
        z-index: 100;

	    .eltdf-pli-title {
			margin: 27px 0 4px;
			text-align: center;
	    }
	
	    .eltdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
			background-color: #fff;
		    line-height: 1;
		    margin: 0 0 3px;
	    }

	    .eltdf-pli-rating {
		    @include eltdfWooCommerceRatingsStyle();
			left: 50%;
			transform: translateX(-50%);
	    }

        .eltdf-pli-price {
	        @include eltdfWooCommercePriceStyle();
			text-align: center;
			margin: 9px 0 0;

			del,ins{
				font-size: 17px;
			}
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .eltdf-pli-text {
				-webkit-clip-path: inset(0 0 0 0);
				clip-path: inset(0 0 0 0);
				transition: .4s cubic-bezier(0.48, 0.57, 0.33, 0.89);
			}

			.eltdf-pli-add-to-cart {
				opacity: 1 !important;
				transform: translateY(0) !important;
		   }
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
			@include eltdfRelativeHolderLayout();
			overflow: hidden;
			

            img {
                display: block;
				width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
	            @include eltdfWooCommerceMarksGeneralStyle();
            }

            .eltdf-pli-onsale {
	            @include eltdfWooCommerceSaleStyle();
            }

            .eltdf-pli-out-of-stock {
	            @include eltdfWooCommerceOutOfStockStyle();
            }
	
	        .eltdf-pli-new-product {
		        @include eltdfWooCommerceNewProductStyle();
	        }
        }
            
        .eltdf-pli-text {
			position: absolute;
			top: 18px;
			left: 18px;
			z-index: 20;
			display: inline-block;
			width: calc(100% - 35px);
			height: calc(100% - 35px);
			padding: 10px;
			background-color: #fff;
			box-sizing: border-box;
			transition: opacity .2s ease-out, background-color .2s ease-out;
			-webkit-clip-path: inset(0 0 100% 0);
			clip-path: inset(0 0 100% 0);
			transition: .2s cubic-bezier(0.48, 0.57, 0.33, 0.89);
        }

        .eltdf-pli-text-outer {
	        @include eltdfTableLayout();
	        text-align: center;
        }

        .eltdf-pli-text-inner {
			@include eltdfTableCellLayout();
			
			.eltdf-pli-add-to-cart {
 				opacity: 0;
            	transform: translateY(10px);
            	transition: .3s;
			}

            .eltdf-pli-title,
            .eltdf-pli-category a,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
            .eltdf-pli-price {
                color: #fff;
            }
	
	        .eltdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .eltdf-pli-add-to-cart {

		        &.eltdf-default-skin {

			        .button,
			        .added_to_cart {
						@include eltdfButtonOutlineColor();
						@include eltdfButtonDefaultStyle();
						padding: 6px 28px;

						@include laptop-landscape-mac{
							padding: 12px 30px;
						}

				        &:hover {
							//@include eltdfButtonSolidHoverColor();
				        }
			        }
		        }

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {


				        &:hover {

				        }
			        }
		        }
		
		        &.eltdf-dark-skin {
			
			        .button,
			        .added_to_cart {

				
				        &:hover {

				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
				@include eltdfButtonSize();
				@include eltdfButtonOutlineColor();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none!important;
            }
        }

	    .eltdf-pli-link {
		    @include eltdfAbsoluteHolderLayout();
	    }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 0;
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: #fff;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: $default-heading-color;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.eltdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/*************** Break Points - begin ***************/

	&.eltdf-grid-list.eltdf-five-columns{
		@include laptop-landscape-mac{
			.eltdf-item-space{
				width: 20%;

				&:nth-child(4n+1){
					clear: unset!important;
				}
				&:nth-child(5n+1){
					clear: both!important;
				}
			}
		}
		@include ipad-landscape{
			.eltdf-item-space{
				width: 50%;

				//&:nth-child(2n+1){
				//	clear: unset!important;
				//}
				&:nth-child(3n+1){
					clear: unset!important;
				}
				&:nth-child(4n+1){
					clear: unset!important;
				}
				&:nth-child(5n+1){
					clear: unset!important;
				}
			}
		}
		@include phone-landscape{
			.eltdf-item-space{
				width: 100%;

				&:nth-child(2n+1){
					clear: unset!important;
				}
				&:nth-child(3n+1){
					clear: unset!important;
				}
				&:nth-child(4n+1){
					clear: unset!important;
				}
				&:nth-child(5n+1){
					clear: unset!important;
				}
			}
		}
	}

	/*************** Break Points - end ***************/


}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */