/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .eltdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $second-main-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .eltdf-quantity-minus{
            border-top: none;
        }

        .eltdf-quantity-input {
            position: relative;
            height: 50px;
            width: 55px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
            border-right: none;
            font-size: 16px;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 35px;
            height: 35px;
            line-height: 35px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .eltdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        font-size: 16px;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */