/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.eltdf-plc-holder {
    @include eltdfRelativeHolderLayout();

    .eltdf-plc-outer {
        @include eltdfRelativeHolderLayout();
    }

    .eltdf-owl-slider{

        .owl-dots{
            margin: 56px 0 0;

            @include phone-landscape{
                display: none;
            }
        }

        .owl-prev{
            left: -122px;

            @include laptop-landscape-large{
                left: -37px;
            }
            @include laptop-landscape-mac{
                left: -86px;
            }
            @include laptop-landscape-medium{
                left: -47px;
            }
            @include phone-landscape{
                left: -32px;
            }
        }
        .owl-next{
            right: -122px;

            @include laptop-landscape-large{
                right: -37px;
            }
            @include laptop-landscape-mac{
                right: -86px;
            }
            @include laptop-landscape-medium{
                right: -47px;
            }
            @include phone-landscape{
                right: -34px;
            }
        }

        .owl-prev,
        .owl-next{
            top: calc(50% - 70px);
        }
    }

    .eltdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        .eltdf-plc-text{
            color: $third-main-color;
        }

        &:hover {

            .eltdf-plc-image-outer {

                .eltdf-plc-add-to-cart {
                    opacity: 1;
                    transform: translateY(0);
                    transition: .3s .1s;
                }

                .eltdf-plc-text {
                    -webkit-clip-path: inset(0 0 0 0);
                    clip-path: inset(0 0 0 0);
                    transition: .4s cubic-bezier(0.48, 0.57, 0.33, 0.89);
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-plc-image-outer {
            @include eltdfRelativeHolderLayout();

            .eltdf-plc-image {
                @include eltdfRelativeHolderLayout();

                .eltdf-plc-onsale,
                .eltdf-plc-out-of-stock,
                .eltdf-plc-new-product {
	                @include eltdfWooCommerceMarksGeneralStyle();
                }

                .eltdf-plc-onsale {
	                @include eltdfWooCommerceSaleStyle();
                    right: 22px;
                    top: 22px;
                }

                .eltdf-plc-out-of-stock {
	                @include eltdfWooCommerceOutOfStockStyle();
                    right: 22px;
                    top: 22px;
                }
	
	            .eltdf-plc-new-product {
		            @include eltdfWooCommerceNewProductStyle();
                    right: 22px;
                    top: 22px;
	            }
            }

            .eltdf-plc-text {
                position: absolute;
                top: 22px;
                left: 22px;
                z-index: 20;
                display: inline-block;
                width: calc(100% - 44px);
                height: calc(100% - 44px);
                padding: 10px;
                background-color: #fff;
                box-sizing: border-box;
                -webkit-clip-path: inset(0 0 100% 0);
				clip-path: inset(0 0 100% 0);
				transition: .2s cubic-bezier(0.48, 0.57, 0.33, 0.89);
            }

            .eltdf-plc-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }

            .eltdf-plc-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }

        .eltdf-plc-title {
            margin: 26px 0 5px;
            text-align: center;
        }
	
	    .eltdf-plc-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 3px;
        }

        .eltdf-plc-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 0 0 3px;
        }

        .eltdf-plc-rating {
	        @include eltdfWooCommerceRatingsStyle();
            left: 50%;
            transform: translateX(-50%);
        }

        .eltdf-plc-price {
	        @include eltdfWooCommercePriceStyle();
            text-align: center;
            letter-spacing: .03em;
            margin: 9px 0 0;

            del,ins{
                font-size: inherit;
                letter-spacing: .03em;
            }
        }
	
	    .button,
	    .added_to_cart {
		    //@include eltdfButtonDefaultStyle();
		    @include eltdfButtonSize();
            @include eltdfButtonOutlineColor();

		    white-space: nowrap;
		    z-index: 3;
	    }
	
	    .button.added {
		    display: none;
	    }
	
	    .eltdf-plc-add-to-cart {
            margin: 0;
            opacity: 0;
            transform: translateY(10px);
            transition: .3s;
		
		    &.eltdf-default-skin {
			
			    .button,
			    .added_to_cart {
                    //@include eltdfButtonOutlineColor();
                    //
				    //&:hover {
					 //   @include eltdfButtonSolidHoverColor();
				    //}
                    @include eltdfButtonDefaultStyle();
                    @include eltdfButtonSize(small);
                    @include eltdfButtonOutlineColor();
                    cursor: pointer;
                    z-index: 3;

                    &:hover {
                        @include eltdfButtonOutlineHoverColor();
                    }

                    &.ajax_add_to_cart.added{
                        display: none;
                    }
			    }
		    }
		
		    &.eltdf-light-skin {
			
			    .button,
			    .added_to_cart {
				    color: $default-heading-color;
				    background-color: #fff;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
		
		    &.eltdf-dark-skin {
			
			    .button,
			    .added_to_cart {
				    color: #fff;
				    background-color: $default-heading-color;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
	    }

        .eltdf-plc-link {
            @include eltdfAbsoluteHolderLayout();
            z-index: 0;
        }
    }

    &.eltdf-standard-layout {

        .eltdf-plc-item {
    
            .eltdf-plc-title,
            .eltdf-plc-category a,
            .eltdf-plc-excerpt,
            .eltdf-plc-rating,
            .eltdf-plc-price {
                //color: #fff;
            }
	
	        .eltdf-plc-category a:hover {
		        //color: rgba(#fff, .8);
	        }
        }
    }

    &.eltdf-simple-layout {
        
        .eltdf-plc-image-outer {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 15px 0 0;
        }

        .eltdf-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 15px;
        }

        .eltdf-plc-text,
        .eltdf-plc-text-outer,
        .eltdf-plc-text-inner {
            @include eltdfRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }
    }
	
	&.eltdf-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	
	&.eltdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			transform: translateY(-50%);
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.eltdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {
					background-color: $header-light-hover-color;
				}

				&:hover,
				&.active {

					span {
						background-color: $header-light-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */